function enumToString(enumId) {
	if (enumId === 'AC') return '계정';
	if (enumId === 'PR') return '프로그램';
	if (enumId === 'GD') return '굿즈';
	if (enumId === 'NT') return '공지사항';
	if (enumId === 'NS') return '대외뉴스';
	if (enumId === 'YT') return '유튜브';
	if (enumId === 'CL') return '칼럼';
	if (enumId === 'PY') return '결제';
	if (enumId === 'SH') return '배송';
	if (enumId === 'ER') return '교환 및 환불';
	if (enumId === 'ETC') return '기타';
	if (enumId === 'T') return '제목';
	if (enumId === 'I') return '아이디';
}

export { enumToString };
