<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li>후기</li>
			<li @click="goto('/admin/question')" class="current">문의</li>
		</ul>
		<div class="contents-container">
			<div class="review-qna-box">
				<h2 class="review-qna-title body3">{{ question.title }}</h2>
				<div class="content-box">
					<div class="content-info">
						<div class="content-info-item">
							<span class="info-title">{{
								showEnumAsString(question.categoryId)
							}}</span>
							<span v-if="false" class="info-value">품목 옵션 </span>

							<span class="info-title">작성자 </span>
							<span class="info-value">{{ question.createId }}</span>
						</div>
						<div class="content-info-item">
							<span class="info-title">게시일 </span>
							<span class="info-value">
								<!-- {{ $moment(item.createDate).format('YYYY.MM.DD HH:MM:SS') }} -->
								2020.02
							</span>
						</div>
					</div>

					<div class="content">
						<p>{{ question.contents }}</p>
					</div>
				</div>
			</div>

			<div class="comment-container">
				<div v-for="(comment, idx) in commentList" :key="idx" class="comment">
					<div class="comment-info">
						<div class="comment-info-item">
							<span class="info-title">작성자</span>
							<span class="info-value">{{ comment.id }}</span>
						</div>
						<div class="comment-info-item">
							<span class="info-title">게시일</span>
							<span class="info-value">2020.02</span>
						</div>
					</div>
					<div class="comment-content" v-if="isEditing(idx)">
						<input class="admin-input" v-model="updateComment" />
					</div>
					<div class="comment-content" v-else>
						{{ comment.contents }}
					</div>
					<div class="button-box">
						<!--            <div class="button-box" v-if="comment.id == getIdentity">-->
						<button
							v-if="!isEditing(idx)"
							class="solid-btn green marR-10"
							@click="edit(idx)"
							:disabled="editingKey !== ''"
						>
							수정
						</button>
						<button v-else class="solid-btn red marR-10" @click="cancel">
							취소
						</button>
						<button
							v-if="!isEditing(idx)"
							class="solid-btn red"
							@click="deleteQuestionComment(commentList[idx].id)"
						>
							삭제
						</button>
						<button
							v-else
							class="solid-btn green"
							@click="updateQuestionComment"
						>
							확인
						</button>
					</div>
				</div>
			</div>
			<div class="comment-input-box">
				<div class="input-item">
					<input
						type="text"
						class="admin-input medium marB-10"
						name="button_title"
						placeholder="댓글을 입력해주세요"
						v-model="comment"
					/>
				</div>
				<button class="outlined-btn" @click="addQuestionComment">
					댓글달기
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import {
	fetchAdminQuestionDetail,
	addQuestionComment,
	updateQuestionComment,
	deleteQuestionComment,
} from '@/api/question';
import { enumToString } from '@/utils/enumToString';

export default {
	created() {
		this.id = this.$route.params.id;
		this.fetchAdminQuestionDetail(this.id);
	},
	computed: {
		...mapGetters(['getIdentity']),
	},
	data() {
		return {
			id: '',
			isLoading: false,
			question: {},
			comment: '',
			commentList: [],
			updateComment: '',
			editableList: [],
			editingKey: '',
			editable: false,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchAdminQuestionDetail(id) {
			const { data } = await fetchAdminQuestionDetail(id);
			this.question = data;
			this.commentList = this.question.commentList;
		},
		async addQuestionComment() {
			let result;
			try {
				let data = {
					contents: this.comment,
				};
				console.log(data);
				result = await addQuestionComment(this.id, data);
				console.log(result);
				if (result.status === 200) {
					alert('댓글이 등록되었습니다.');
					this.fetchAdminQuestionDetail(this.id);
				}
			} catch (e) {
				console.log(e);
			}
		},
		edit(key) {
			this.editingKey = key;
			this.updateComment = this.commentList[key].contents;
		},
		isEditing(key) {
			return key === this.editingKey;
		},
		cancel() {
			this.editingKey = '';
		},
		showEnumAsString(enumId) {
			return enumToString(enumId);
		},
		async updateQuestionComment() {
			try {
				let data = {
					contents: this.updateComment,
				};
				await updateQuestionComment(this.commentList[this.editingKey].id, data);
			} catch (e) {
				console.log(e);
			} finally {
				await this.fetchAdminQuestionDetail(this.id);
			}
			this.editingKey = '';
		},
		async deleteQuestionComment(commentId) {
			try {
				await deleteQuestionComment(commentId);
			} catch (e) {
				console.log(e);
			} finally {
				window.location.reload();
			}
		},
	},
	watch: {
		editingKey() {
			this.editable = this.isEditing(this.editingKey);
		},
	},
};
</script>

<style lang="scss" scoped>
.review-qna-box {
	.review-qna-title {
		padding: 0 20px 20px 20px;
		border-bottom: 1px solid #e6e6e6;
	}

	.content-box {
		padding: 20px;
		.content-info {
			display: flex;
		}
		.content-info-item {
			margin-bottom: 20px;
			margin-right: 20px;
		}

		.content {
			color: #646464;
		}
	}
}
.comment-container {
	border-top: 1px solid #e6e6e6;
	margin-bottom: 40px;

	.comment {
		padding: 20px;
		box-sizing: border-box;
		background: #fafafa;
		border-bottom: 1px solid #e6e6e6;
	}
	.comment-info {
		display: flex;
		margin-bottom: 10px;
	}
	.comment-info-item {
		margin-right: 20px;
	}
}

.info-title {
	margin-right: 10px;
	color: #8c8c8c;
}

.button-box {
	margin-top: 10px;
}
</style>
